.clear		            - All button states can become hollow
.dropdown		        - All button states can become hollow
.radius		            - Give the button softer corners
.shadow.clear		    - Give a button a shadow
.tiny		            - tiny buttons
.small		            - Small buttons
default		            - Medium buttons (default)
.large		            - Large buttons
