/*
  Loading Grid Dots
 
  Animated Loading Dots

  white              - text color modified light to dark
  black              - text color modified light to dark


  markup:
    <div class="lds-grid">
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
        <div class="dot bg_{$modifiers}-6 br_solid br_2 br_{$modifiers} br_circle"></div>
    </div>

    sg-wrapper:
    <div class="p_5 bg_primary texture_medium"><sg-wrapper-content/></div>
 
  Styleguide 20.40
  
  */
.lds-grid {
  display: grid;
  grid-template-columns: 16px 16px 16px;
  grid-gap: 12px;
  position: relative;
  width: 64px;
  height: 64px;
  margin: 0 auto;
}

.lds-grid .dot {
  width: 16px;
  height: 16px;
